import { Component, OnDestroy, OnInit } from "@angular/core";
import { MatBottomSheet } from "@angular/material/bottom-sheet";
import { Subscription } from "rxjs";
import { tap } from "rxjs/operators";
import { BottomSheetDataComponent } from "../bottom-sheet-data/bottom-sheet-data.component";
import { ApiService } from "../services/api.service";

@Component({
  selector: "app-update",
  templateUrl: "./update.component.html",
  styleUrls: ["./update.component.scss"],
})
export class UpdateComponent implements OnInit, OnDestroy {
  // public isUpdateRequired = false;
  // public isCheckingUpdateRequired = true;
  public motd = "";
  public updating = false;
  private subs: Subscription[] = [];

  constructor(
    private service: ApiService,
    private bottomSheet: MatBottomSheet
  ) {}

  ngOnInit() {
    // const sub1 = this.service
    //   .getUpdateRequired$()
    //   .pipe(
    //     tap((result) => {
    //       this.isUpdateRequired = result;
    //       this.isCheckingUpdateRequired = false;
    //     })
    //   )
    //   .subscribe(
    //     (result) => {},
    //     (error) => {
    //       console.log(error);
    //     }
    //   );
    // this.subs.push(sub1);

    const sub2 = this.service
      .getMotd$()
      .pipe(
        tap((result) => {
          this.motd = result;
        })
      )
      .subscribe(
        (result) => {},
        (error) => {
          console.log(error);
        }
      );
    this.subs.push(sub2);
  }

  ngOnDestroy() {
    this.subs.forEach((sub) => {
      sub.unsubscribe();
    });
  }

  public doUpdate() {
    this.updating = true;
    this.service.doUpdate$().subscribe(
      (result) => {
        let msg = "";
        if (result) {
          msg = `We got the update and are applying it now. If the update takes a long time, you may need to refresh the page yourself in a minute or two.`;
        } else {
          msg = `Seems like you're already up to date.`;
        }
        this.bottomSheet.open(BottomSheetDataComponent, {
          data: { message: msg, progress: result },
        });
        if (result) {
          setTimeout(() => {
            this.updating = false;
            window.location.reload();
          }, 5000);
        } else {
          setTimeout(() => {
            this.updating = false;
            this.bottomSheet.dismiss();
          }, 1600);
        }
      },
      (error) => {
        console.log(error);
      }
    );
  }
}
